/* Global Variables */
/* Colors */
body {
  margin: 0;
}
body #app {
    color: #2e3438;
    font-family: "Droid Sans Regular", sans-serif;
    font-size: 14pt;
}
body #app .nav-container {
      background: -webkit-gradient(linear, left top, left bottom, from(#000000), to(#2e3438));
      background: linear-gradient(#000000, #2e3438);
      height: auto;
      margin-bottom: 15px;
      width: 100%;
}
body #app .nav-container .zeek-text {
        color: #ffffff;
        font-size: 7pt;
        font-weight: 200;
        line-height: 1.3;
        padding-top: 33px;
}
@media screen and (min-width: 360px) {
body #app .nav-container .zeek-text {
            float: right;
            font-size: 8pt;
}
}
@media screen and (min-width: 406px) {
body #app .nav-container .zeek-text {
            font-size: 10pt;
            padding-top: 32px;
}
}
@media screen and (min-width: 530px) {
body #app .nav-container .zeek-text {
            font-size: 16pt;
            padding-top: 37px;
}
}
@media screen and (min-width: 768px) {
body #app .nav-container .zeek-text {
            font-size: 22pt;
            padding-top: 30px;
}
}
body #app .nav-container .navbar-wrapper {
        clear: both;
        padding-top: 2px;
}
body #app .nav-container .navbar-wrapper ul {
          padding: 20px 0;
          text-align: center;
          width: auto;
}
@media screen and (min-width: 530px) {
body #app .nav-container .navbar-wrapper ul {
              text-align: initial;
}
}
body #app .nav-container .navbar-wrapper ul li {
            border-left: 2px solid #2e3438;
            display: inline;
            font-weight: 200;
            padding: 15px 28px;
}
body #app .nav-container .navbar-wrapper ul li .nav-link {
              text-decoration: none;
}
body #app .nav-container .navbar-wrapper ul li.first {
              border-left: none;
              padding-left: 0.5em;
}
@media screen and (min-width: 530px) {
body #app .nav-container .navbar-wrapper ul li.first {
                  border-left: 2px solid #2e3438;
}
}
@media screen and (min-width: 530px) {
body #app .nav-container .navbar-wrapper ul li {
                padding: 15px 40px;
}
}
@media screen and (min-width: 530px) {
body #app .nav-container .navbar-wrapper ul li.last {
                border-right: 2px solid #2e3438;
}
}
/* Global Variables */
/* Colors */
.product-cards .product {
  background-color: #ffffff;
  -webkit-box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.5);
          box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.5);
  color: #2e3438;
  font-size: 18pt;
  margin-bottom: 10px;
  margin-right: 10px;
  padding-left: 14px;
  padding-top: 15px;
}
.product-cards .product .row img {
    max-height: 15em;
    width: 100%;
}
